<template>
  <div class="home">
    <img
      class="img-fluid flex-img mt-5"
      alt="Vue logo"
      src="../assets/brawl_transp_gun.png"
    />
    <h1 class="text-white">hello</h1>
    <section
      class="section container-fluid mt-5"
      v-for="section in SECTIONS"
      :key="section.title"
    >
      <div class="row section-title text-white">
        <span class="col section-title-text display-4">{{
          section.title.toUpperCase()
        }}</span>
      </div>
      <div
        :class="
          'row mx-auto my-2 p-1 p-lg-2 section-items fs-4  rounded-pill  bg-secondary text-white section-items-player-' +
          (index + 1)
        "
        v-for="(player, index) in section.PLAYER_LIST"
        :key="player.name"
      >
        <div class="col-9 p-0 text-start">
          {{ getEmoji(index) + player.name }}
        </div>
        <div class="col-3 ps-0 pe-1 text-end">{{ player[section.stat] }}</div>
      </div>
    </section>
  </div>
</template>

<script>
// @ is an alias to /src
//https://api.brawlstars.com/v1/players/2GPPL89QY

import axios from "axios";
import { ref } from "@vue/reactivity";

export default {
  name: "Home",
  setup() {
    const STATS = {
      trophies: "Troféus",
      highestTrophies: "Max Troféus",
      "3vs3Victories": "3vs3",
      soloVictories: "Combate Solo",
      duoVictories: "Combate Duplo",
      brawlers: "Brawlers",
    };
    const SECTIONS = ref([]);
    const sortByProperty = (array, property) =>
      Array.from(array).sort(function (a, b) {
        if (a[property] > b[property]) return -1;
        if (b[property] > a[property]) return 1;
        return 0;
      });

    axios
      .get("api/players/")
      .then((res) => res.data)
      .then((data) => {
        var players = [...data];

        Object.keys(STATS).forEach(function (stat) {
          // Change player stat to number of items
          const countVariable = "brawlers";

          if (stat === countVariable) {
            players.forEach(
              (player) => (player[countVariable] = player[countVariable].length)
            );
          }

          SECTIONS.value.push({
            title: STATS[stat],
            stat: stat,
            PLAYER_LIST: sortByProperty(players, stat),
          });
        });
      });

    const getEmoji = (pos) => {
      // codes by https://emojiterra.com
      switch (pos + 1) {
        case 1:
          return "\uD83C\uDFC6";
        case 2:
          return "\uD83E\uDD48";
        case 3:
          return "\uD83E\uDD49";
        default:
          return pos + 1 + ". ";
      }
    };
    return { SECTIONS, getEmoji };
  },
};
</script>

